:root {
  --color-orange: #E87722;
  --color-yellow: #F5CD26;
  --color-black: #231F20;
  --color-text: #231F20;
  --color-white: #FFFFFF;
  --color-gray: #E5E5E5;
  --color-gray-light-1: #FAFAFA;
  --color-gray-light-2: #F7F7F7;
  --color-gray-light-3: #F2F2F2;
  --color-gray-light-4: #EAEAEA;
  --color-gray-darker: #999999;
  --color-gray-darker-2: #848283;
  --color-blue: #0051CC;
  --color-blue-hover: #0065FF;
  --body-font-family: "Open Sans", sans-serif, ;
}

.node--view-mode-teaser {
  padding: 20px;
  border: 1px solid var(--color-gray);
  border-radius: 10px;
  height: 100%;
}
.node--view-mode-teaser picture {
  margin-bottom: 20px;
}
.node--view-mode-teaser a picture {
  position: relative;
  display: block;
}
.node--view-mode-teaser a:hover img {
  border: 4px solid var(--color-yellow);
}
.node--view-mode-teaser a:hover picture:after {
  content: "";
  background: var(--color-yellow);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  z-index: 100;
  border-radius: 10px;
  opacity: 0.4;
}
.node--view-mode-teaser h3 {
  line-height: 1;
  margin-bottom: 10px;
}
.node--view-mode-teaser img {
  border: 4px solid var(--color-orange);
  border-radius: 10px;
  width: 100%;
  height: auto;
}
.node--view-mode-teaser .date-tags {
  display: flex;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 15px;
}
.node--view-mode-teaser .date-tags .field + .field {
  border-left: 1px solid var(--color-black);
  margin-left: 5px;
  padding-left: 5px;
}
.node--view-mode-teaser .title-teaser {
  font-size: 20px;
}
.node--view-mode-teaser .field--name-field-tags {
  text-transform: uppercase;
}
.node--view-mode-teaser .field--name-field-tags > div:not(:first-child) {
  display: none;
}

.node--view-mode-featured-teaser .field--name-field-tags {
  text-transform: uppercase;
}
.node--view-mode-featured-teaser .field--name-field-tags > div:not(:first-child) {
  display: none;
}
.node--view-mode-featured-teaser.node--type-success-story {
  padding: 20px;
  border: 1px solid var(--color-orange);
  border-radius: 10px;
  font-size: 18px;
  line-height: 20px;
}
.node--view-mode-featured-teaser.node--type-success-story h3 {
  text-align: center;
  padding-bottom: 20px;
  border-bottom: 1px solid var(--color-gray);
  margin-bottom: 20px;
}
.node--view-mode-featured-teaser.node--type-success-story h3 a {
  color: var(--color-orange);
}
.node--view-mode-featured-teaser.node--type-success-story h3 a:hover, .node--view-mode-featured-teaser.node--type-success-story h3 a:active {
  text-decoration: none;
}